// VideoOrIframe.jsx
import React from 'react';
import PropTypes from 'prop-types';
import styled from '@emotion/styled';

// Styled components for video and iframe
const StyledVideo = styled.video`
  border-radius: 1rem;
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const StyledIframe = styled.iframe`
  border-radius: 1rem;
  width: 32rem;
  height: 18rem;
`;

const VideoOrIframe = ({ vidPath, vidPreview }) => {
  // Helper function to check if vidPath starts with '1'
  const startsWithOne = (path) => {
    return typeof path === 'string' && path.startsWith('1');
  };

  if (!vidPath) return null; // Render nothing if vidPath is empty

  if (startsWithOne(vidPath)) {
    // Render iframe if vidPath starts with '1'
    return (
      <StyledIframe
        src={`https://www.youtube.com/embed/${vidPath.substring(1)}`}
        title="YouTube video player"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        referrerPolicy="strict-origin-when-cross-origin"
        allowFullScreen
      ></StyledIframe>
    );
  } else {
    // Render video element if vidPath does not start with '1'
    return (
      <StyledVideo poster={vidPreview} muted controls loop>
        <source src={vidPath} type="video/mp4" />
        Your browser does not support the video tag.
      </StyledVideo>
    );
  }
};

VideoOrIframe.propTypes = {
  vidPath: PropTypes.string.isRequired,
  vidPreview: PropTypes.string,
};

export default VideoOrIframe;
