import React from 'react';
import styled from '@emotion/styled';
import PropTypes from 'prop-types';
import Fade from 'react-reveal/Fade';

// Import the VideoOrIframe component
import VideoOrIframe from './VideoOrIframe'; // Adjust the path as necessary

const TagsContainer = styled.div`
  @media all and (max-width: 775px) {
    max-width: 100vw;
    padding: 0%;
  }
`;

const InnerContainer = styled.div`
  display: flex;
  flex-wrap: nowrap;
  padding-top: 3rem;
  padding-bottom: 3rem;
  justify-content: space-evenly;
  border-top: 0.01px solid white;
  margin-left: 5%;
  margin-right: 5%;
  background: #FFF4F2;

  @media all and (max-width: 775px) {
    flex-direction: column !important;
    margin-left: 5%;
    margin-right: 10%;
  }
`;

const OuterContainer = styled.div``;

const Info1 = styled.div`
  flex-direction: column !important;
  align-items: center;
  margin-top: 1rem;

  @media all and (max-width: 775px) {
    margin-top: 0px;
  }
`;

const VideoDiv = styled.div`
  max-width: 100vw;
  padding: 1%;

  @media all and (max-width: 775px) {
    display: flex;
  }
`;

const Loading = styled.div`
  display: flex;
  flex-wrap: nowrap;
  height: 25rem;
`;

const Feature = styled.div`
  display: flex;
  padding: 0 10%;
  flex-wrap: nowrap;
  flex-direction: column !important;
`;

const FeatureHeader = styled.div`
  display: flex;
  flex-wrap: nowrap;
  padding: 0 10%;
  
  font-size: 1.2rem;
`;

const FeaturesBlock = ({ list }) => (
  <TagsContainer>
    {list &&
      list.map((tag, index) => {
        const upperTag1 = tag.featureHeader;
        const upperTag = tag.feature;
        const vidPath = tag.videoPath;
        const vidPreview = tag.videoPreview;
        const y = tag.y;
        const x = tag.x;

        return (
          <OuterContainer key={index} style={{ background: `#${tag.background}` }}>
            <InnerContainer
              style={{
                background: `#${tag.background}`,
                flexDirection: tag.justifyCont,
                borderTopColor: `#${tag.borderCol}`,
              }}
            >
              <Info1>
                <Fade big>
                  <FeatureHeader style={{ color: `#${tag.featureHeadCol}` }}>
                    {upperTag1}
                  </FeatureHeader>

                  <Feature style={{ color: `#${tag.featureCol}` }}>
                    <a style={{ paddingBottom: '1rem' }}>{upperTag}</a>
                    {tag.features &&
                      tag.features.map((des, descIndex) => {
                        const desc = des.feature;

                        return <li key={descIndex}>{desc}</li>;
                      })}
                  </Feature>
                </Fade>
              </Info1>
              <Fade>
                <VideoDiv>
                  {/* Use the VideoOrIframe component */}
                  <VideoOrIframe vidPath={vidPath} vidPreview={vidPreview} />
                </VideoDiv>
              </Fade>
            </InnerContainer>
          </OuterContainer>
        );
      })}
  </TagsContainer>
);

export default FeaturesBlock;

FeaturesBlock.propTypes = {
  list: PropTypes.array,
};
